.about-section-dark {
  min-height: 100vh;
  padding: 6rem 2rem;
  position: relative;
}

.about-container {
  max-width: 1200px;
  margin: 0 auto;
}

// Section Header
.section-header {
  text-align: center;
  margin-bottom: 4rem;

  .section-number {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.85rem;
    color: #6366f1;
    letter-spacing: 4px;
    display: block;
    margin-bottom: 0.5rem;
  }

  .section-title {
    font-size: 2.5rem;
    font-weight: 700;
    color: #fff;
    margin-bottom: 0.5rem;
  }

  .section-subtitle {
    color: rgba(255, 255, 255, 0.5);
    font-size: 1rem;
  }
}

// About Content Layout
.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  margin-bottom: 4rem;

  @media (max-width: 968px) {
    grid-template-columns: 1fr;
  }
}

// Crew Card
.crew-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  overflow: hidden;
  backdrop-filter: blur(10px);

  .card-header {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    padding: 2rem;
    background: rgba(99, 102, 241, 0.05);
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  }

  .avatar-container {
    position: relative;
    width: 80px;
    height: 80px;
  }

  .avatar-ring {
    position: absolute;
    inset: -5px;
    border: 2px solid rgba(99, 102, 241, 0.3);
    border-radius: 50%;
    overflow: hidden;

    .ring-animation {
      position: absolute;
      inset: 0;
      border: 2px solid transparent;
      border-top-color: #6366f1;
      border-radius: 50%;
    }
  }

  .avatar {
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, #6366f1, #22d3ee);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    .avatar-initials {
      font-size: 1.5rem;
      font-weight: 700;
      color: #fff;
    }
  }

  .crew-info {
    .crew-name {
      font-size: 1.5rem;
      font-weight: 600;
      color: #fff;
      margin-bottom: 0.25rem;
    }

    .crew-rank {
      display: block;
      font-size: 0.9rem;
      color: #22d3ee;
      margin-bottom: 0.5rem;
    }

    .crew-status {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      font-size: 0.75rem;
      color: rgba(255, 255, 255, 0.5);

      .status-dot {
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: #22c55e;
        animation: pulse 2s infinite;
      }
    }
  }

  .card-body {
    padding: 2rem;
  }

  .bio-section {
    margin-bottom: 2rem;

    .bio-title {
      font-family: 'JetBrains Mono', monospace;
      font-size: 0.75rem;
      color: rgba(255, 255, 255, 0.4);
      letter-spacing: 2px;
      text-transform: uppercase;
      margin-bottom: 0.75rem;
    }

    .bio-text {
      font-size: 0.95rem;
      color: rgba(255, 255, 255, 0.7);
      line-height: 1.7;
    }
  }

  .clearance-section {
    .clearance-title {
      font-family: 'JetBrains Mono', monospace;
      font-size: 0.75rem;
      color: rgba(255, 255, 255, 0.4);
      letter-spacing: 2px;
      text-transform: uppercase;
      margin-bottom: 0.75rem;
    }

    .clearance-bars {
      display: flex;
      gap: 4px;
      margin-bottom: 0.5rem;
    }

    .clearance-bar {
      flex: 1;
      height: 8px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 2px;
      transform-origin: left;

      &.active {
        background: linear-gradient(90deg, #6366f1, #22d3ee);
      }
    }

    .clearance-label {
      font-family: 'JetBrains Mono', monospace;
      font-size: 0.7rem;
      color: #22d3ee;
      letter-spacing: 2px;
    }
  }

  .card-footer {
    display: flex;
    justify-content: space-between;
    padding: 1rem 2rem;
    background: rgba(0, 0, 0, 0.2);
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.7rem;
    color: rgba(255, 255, 255, 0.3);
    letter-spacing: 1px;
  }
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

// Stats Grid
.stats-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;

  @media (max-width: 600px) {
    grid-template-columns: 1fr;
  }
}

.stat-card {
  position: relative;
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  display: flex;
  gap: 1rem;
  overflow: hidden;
  transition: all 0.3s ease;

  .stat-icon {
    width: 50px;
    height: 50px;
    background: rgba(99, 102, 241, 0.1);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #6366f1;
    font-size: 1.25rem;
    flex-shrink: 0;
  }

  .stat-content {
    display: flex;
    flex-direction: column;
  }

  .stat-value {
    font-size: 1.75rem;
    font-weight: 700;
    color: #fff;
    font-family: 'JetBrains Mono', monospace;
  }

  .stat-label {
    font-size: 0.85rem;
    color: rgba(255, 255, 255, 0.7);
    margin-bottom: 0.25rem;
  }

  .stat-detail {
    font-size: 0.7rem;
    color: rgba(255, 255, 255, 0.4);
  }

  .stat-glow {
    position: absolute;
    top: 0;
    right: 0;
    width: 100px;
    height: 100px;
    background: radial-gradient(circle, rgba(99, 102, 241, 0.15), transparent);
    border-radius: 50%;
    transform: translate(30%, -30%);
  }
}

// Skills Radar
.skills-radar {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 2rem;

  .radar-title {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.85rem;
    color: rgba(255, 255, 255, 0.5);
    letter-spacing: 3px;
    text-transform: uppercase;
    margin-bottom: 1.5rem;
    text-align: center;
  }
}

.competency-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

.competency-item {
  .competency-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.5rem;
  }

  .competency-name {
    font-size: 0.85rem;
    color: rgba(255, 255, 255, 0.8);
  }

  .competency-level {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.75rem;
    color: #6366f1;
  }

  .competency-bar {
    height: 6px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 3px;
    overflow: hidden;
  }

  .competency-fill {
    height: 100%;
    background: linear-gradient(90deg, #6366f1, #22d3ee);
    border-radius: 3px;
  }
}
