.space-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
  background: #0a0a1a;
}

// Base space gradient
.space-base {
  position: absolute;
  inset: 0;
  background: radial-gradient(ellipse at 50% 0%, #1a1a3a 0%, #0a0a1a 50%, #050510 100%);
}

// === PHASE LAYERS ===
.space-phase {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

// Phase 1: Launch
.phase-launch {
  .atmosphere-gradient {
    position: absolute;
    inset: 0;
    background: linear-gradient(
      to top,
      rgba(255, 100, 50, 0.15) 0%,
      rgba(100, 150, 255, 0.1) 30%,
      transparent 60%
    );
  }

  .launch-pad-glow {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 200px;
    height: 100px;
    background: radial-gradient(ellipse, rgba(255, 150, 50, 0.4) 0%, transparent 70%);
    filter: blur(20px);
    animation: launchGlow 2s ease-in-out infinite;
  }
}

@keyframes launchGlow {
  0%, 100% { opacity: 0.6; transform: translateX(-50%) scale(1); }
  50% { opacity: 1; transform: translateX(-50%) scale(1.2); }
}

// Phase 2: Orbit
.phase-orbit {
  .orbit-gradient {
    position: absolute;
    inset: 0;
    background: radial-gradient(circle at 50% 120%, rgba(74, 144, 217, 0.1) 0%, transparent 50%);
  }

  .earth {
    position: absolute;
    bottom: -20%;
    left: 50%;
    transform: translateX(-50%);
    width: 400px;
    height: 400px;

    @media (max-width: 768px) {
      width: 300px;
      height: 300px;
      bottom: -15%;
    }
  }
}

// Phase 3: Deep Space
.phase-deep {
  .deep-space-gradient {
    position: absolute;
    inset: 0;
    background: radial-gradient(ellipse at 30% 30%, rgba(100, 50, 150, 0.1) 0%, transparent 50%),
                radial-gradient(ellipse at 70% 70%, rgba(50, 100, 150, 0.1) 0%, transparent 50%);
  }

  .asteroids {
    position: absolute;
    inset: 0;
  }

  .asteroid {
    position: absolute;
  }
}

// Phase 4: Destination
.phase-destination {
  .galaxy-gradient {
    position: absolute;
    inset: 0;
    background: radial-gradient(ellipse at 50% 50%, rgba(157, 78, 221, 0.15) 0%, transparent 60%);
  }

  .destination-planet {
    position: absolute;
    top: 10%;
    right: 10%;
    width: 250px;
    height: 250px;

    @media (max-width: 768px) {
      width: 150px;
      height: 150px;
      top: 5%;
      right: 5%;
    }
  }
}

// === NEBULA LAYERS ===
.nebula-layer {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.nebula {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.15;
  mix-blend-mode: screen;

  &.nebula-1 {
    top: 10%;
    left: 5%;
    width: 400px;
    height: 300px;
    background: radial-gradient(ellipse, #6366f1 0%, transparent 70%);
    animation: nebulaFloat1 25s ease-in-out infinite;
  }

  &.nebula-2 {
    top: 40%;
    right: 10%;
    width: 350px;
    height: 350px;
    background: radial-gradient(ellipse, #f472b6 0%, transparent 70%);
    animation: nebulaFloat2 30s ease-in-out infinite;
  }

  &.nebula-3 {
    bottom: 20%;
    left: 30%;
    width: 300px;
    height: 250px;
    background: radial-gradient(ellipse, #22d3ee 0%, transparent 70%);
    animation: nebulaFloat3 20s ease-in-out infinite;
  }
}

@keyframes nebulaFloat1 {
  0%, 100% { transform: translate(0, 0) scale(1); }
  50% { transform: translate(30px, -20px) scale(1.1); }
}

@keyframes nebulaFloat2 {
  0%, 100% { transform: translate(0, 0) scale(1); }
  50% { transform: translate(-40px, 30px) scale(1.15); }
}

@keyframes nebulaFloat3 {
  0%, 100% { transform: translate(0, 0) scale(1); }
  50% { transform: translate(20px, 40px) scale(1.05); }
}

// === STAR LAYERS ===
.star-layer {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.star {
  position: absolute;
  background: #fff;
  border-radius: 50%;

  &.star-bright {
    background: radial-gradient(circle, #fff 0%, rgba(255, 255, 255, 0.8) 50%, transparent 100%);
  }

  &.star-glow {
    background: #fff;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
  }
}

// === SHOOTING STARS ===
.shooting-stars {
  position: absolute;
  inset: 0;
  pointer-events: none;
  overflow: hidden;
}

.shooting-star {
  position: absolute;
  width: 3px;
  height: 3px;
  background: #fff;
  border-radius: 50%;
  opacity: 0;
  animation: shootingStar 8s linear infinite;

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 100%;
    width: 100px;
    height: 2px;
    background: linear-gradient(to left, rgba(255, 255, 255, 0.8), transparent);
    transform: translateY(-50%);
  }
}

@keyframes shootingStar {
  0% {
    opacity: 0;
    transform: translate(0, 0) rotate(-45deg);
  }
  2% {
    opacity: 1;
  }
  10% {
    opacity: 1;
    transform: translate(-300px, 300px) rotate(-45deg);
  }
  12% {
    opacity: 0;
    transform: translate(-350px, 350px) rotate(-45deg);
  }
  100% {
    opacity: 0;
    transform: translate(-350px, 350px) rotate(-45deg);
  }
}

// === PHASE INDICATOR ===
.phase-indicator {
  position: fixed;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 20px;
  z-index: 100;

  @media (max-width: 768px) {
    right: 10px;
    gap: 15px;
  }
}

.indicator-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  border: 2px solid rgba(255, 255, 255, 0.3);
  cursor: pointer;
  position: relative;
  transition: all 0.3s ease;

  &:hover {
    background: rgba(255, 255, 255, 0.4);

    .indicator-label {
      opacity: 1;
      transform: translateX(-10px);
    }
  }

  &.active {
    background: #6366f1;
    border-color: #6366f1;
    box-shadow: 0 0 15px rgba(99, 102, 241, 0.5);
  }

  .indicator-label {
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%) translateX(0);
    padding-right: 15px;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.7);
    white-space: nowrap;
    opacity: 0;
    transition: all 0.3s ease;
    font-family: 'JetBrains Mono', monospace;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
}

// === SATELLITE (Easter egg) ===
.satellite {
  position: absolute;
  animation: satelliteOrbit 60s linear infinite;
}

@keyframes satelliteOrbit {
  0% { transform: rotate(0deg) translateX(300px) rotate(0deg); }
  100% { transform: rotate(360deg) translateX(300px) rotate(-360deg); }
}

// Responsive
@media (max-width: 768px) {
  .nebula {
    filter: blur(60px);
    opacity: 0.1;

    &.nebula-1 { width: 250px; height: 200px; }
    &.nebula-2 { width: 200px; height: 200px; }
    &.nebula-3 { width: 180px; height: 150px; }
  }
}
