.contact-section-dark {
  min-height: 100vh;
  padding: 6rem 2rem;
  position: relative;
  overflow: hidden;
}

.contact-container {
  max-width: 900px;
  margin: 0 auto;
  position: relative;
  z-index: 10;
}

// Section Header
.section-header {
  text-align: center;
  margin-bottom: 3rem;

  .section-number {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.85rem;
    color: #6366f1;
    letter-spacing: 4px;
    display: block;
    margin-bottom: 0.5rem;
  }

  .section-title {
    font-size: 2.5rem;
    font-weight: 700;
    color: #fff;
    margin-bottom: 0.5rem;
  }

  .section-subtitle {
    color: rgba(255, 255, 255, 0.5);
    font-size: 1rem;
  }
}

// Transmission Status
.transmission-status {
  text-align: center;
  margin-bottom: 3rem;

  .status-display {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 0.75rem;
  }

  .signal-bars {
    display: flex;
    align-items: flex-end;
    gap: 3px;
    height: 20px;

    .bar {
      width: 4px;
      background: #22c55e;
      border-radius: 2px;

      &.bar-1 { height: 4px; }
      &.bar-2 { height: 8px; }
      &.bar-3 { height: 12px; }
      &.bar-4 { height: 16px; }
      &.bar-5 { height: 20px; }
    }
  }

  .status-text {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.75rem;
    color: #22c55e;
    letter-spacing: 2px;
  }

  .coordinates {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.7rem;
    color: rgba(255, 255, 255, 0.4);
    letter-spacing: 1px;

    .divider {
      margin: 0 0.5rem;
      color: rgba(255, 255, 255, 0.2);
    }
  }
}

// Channels Grid
.channels-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
  margin-bottom: 3rem;

  @media (max-width: 600px) {
    grid-template-columns: 1fr;
  }
}

.channel-card {
  position: relative;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.3s ease;

  .channel-link,
  .channel-content {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1.25rem;
    text-decoration: none;
    color: inherit;
  }

  .channel-icon {
    position: relative;
    width: 45px;
    height: 45px;
    background: rgba(34, 211, 238, 0.1);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #22d3ee;
    font-size: 1.1rem;
    flex-shrink: 0;

    .icon-pulse {
      position: absolute;
      inset: 0;
      border: 2px solid #22d3ee;
      border-radius: 10px;
    }
  }

  .channel-info {
    flex: 1;

    .channel-label {
      display: block;
      font-size: 0.7rem;
      color: rgba(255, 255, 255, 0.4);
      text-transform: uppercase;
      letter-spacing: 2px;
      margin-bottom: 0.25rem;
    }

    .channel-value {
      font-size: 0.95rem;
      color: #fff;
    }
  }

  .channel-frequency {
    text-align: right;

    .freq-label {
      display: block;
      font-family: 'JetBrains Mono', monospace;
      font-size: 0.6rem;
      color: rgba(255, 255, 255, 0.3);
      letter-spacing: 1px;
    }

    .freq-value {
      font-family: 'JetBrains Mono', monospace;
      font-size: 0.75rem;
      color: #6366f1;
    }
  }

  .signal-indicator {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    background: linear-gradient(90deg, #6366f1, #22d3ee);
  }
}

// Transmission Terminal
.transmission-terminal {
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 3rem;

  .terminal-header {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.75rem 1rem;
    background: rgba(255, 255, 255, 0.05);
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);

    .dot {
      width: 10px;
      height: 10px;
      border-radius: 50%;

      &.red { background: #ff5f56; }
      &.yellow { background: #ffbd2e; }
      &.green { background: #27c93f; }
    }

    .terminal-title {
      margin-left: auto;
      font-family: 'JetBrains Mono', monospace;
      font-size: 0.75rem;
      color: rgba(255, 255, 255, 0.4);
    }
  }

  .terminal-body {
    padding: 1.5rem;
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.85rem;
  }

  .terminal-line {
    margin-bottom: 0.75rem;
    color: rgba(255, 255, 255, 0.8);

    .prompt {
      color: #22d3ee;
      margin-right: 0.5rem;
    }

    &.output {
      color: rgba(255, 255, 255, 0.6);
      padding-left: 1rem;
    }

    .success {
      color: #22c55e;
    }

    .cursor {
      animation: blink 1s step-end infinite;
    }
  }
}

@keyframes blink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0; }
}

// CTA Section
.cta-section {
  text-align: center;
  margin-bottom: 4rem;

  .cta-text {
    color: rgba(255, 255, 255, 0.6);
    font-size: 1.1rem;
    margin-bottom: 1.5rem;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
  }

  .cta-button {
    display: inline-flex;
    align-items: center;
    gap: 0.75rem;
    padding: 1rem 2rem;
    background: linear-gradient(135deg, rgba(99, 102, 241, 0.2), rgba(34, 211, 238, 0.2));
    border: 1px solid rgba(34, 211, 238, 0.4);
    border-radius: 8px;
    color: #22d3ee;
    text-decoration: none;
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.9rem;
    letter-spacing: 2px;
    transition: all 0.3s ease;

    .button-signal {
      color: #22c55e;
    }
  }
}

// Footer
.section-footer {
  text-align: center;

  .footer-text {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.3);
    letter-spacing: 1px;
    margin-bottom: 1rem;
  }

  .footer-line {
    width: 100px;
    height: 1px;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    margin: 0 auto;
  }
}

// Signal Waves Background
.signal-waves {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;

  .wave {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 300px;
    height: 300px;
    border: 1px solid rgba(99, 102, 241, 0.2);
    border-radius: 50%;
  }
}
