.hero-section-dark {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 2rem;
  overflow: hidden;
}

.hero-content {
  text-align: center;
  z-index: 10;
  max-width: 900px;
}

// Mission Header
.mission-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.mission-badge {
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.85rem;
  color: #6366f1;
  letter-spacing: 6px;
  padding: 0.5rem 1.5rem;
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: 4px;
  background: rgba(99, 102, 241, 0.1);
}

.status-indicator {
  display: flex;
  align-items: center;
  gap: 0.5rem;

  .status-dot {
    width: 8px;
    height: 8px;
    background: #22c55e;
    border-radius: 50%;
    animation: statusPulse 2s ease-in-out infinite;
  }

  .status-text {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.75rem;
    color: #22c55e;
    letter-spacing: 2px;
  }
}

@keyframes statusPulse {
  0%, 100% { opacity: 1; box-shadow: 0 0 5px #22c55e; }
  50% { opacity: 0.5; box-shadow: 0 0 15px #22c55e; }
}

// Hero Name
.hero-name {
  margin-bottom: 1.5rem;

  .name-prefix {
    display: block;
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.9rem;
    color: rgba(255, 255, 255, 0.5);
    letter-spacing: 4px;
    margin-bottom: 0.5rem;
  }

  .name-main {
    display: block;
    font-size: 4rem;
    font-weight: 700;
    background: linear-gradient(135deg, #fff 0%, #6366f1 50%, #22d3ee 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: none;
    letter-spacing: 2px;

    @media (max-width: 768px) {
      font-size: 2.5rem;
    }
  }
}

// Hero Role
.hero-role {
  margin-bottom: 3rem;

  .role-label {
    display: block;
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.4);
    letter-spacing: 3px;
    margin-bottom: 0.5rem;
  }

  .role-text {
    font-family: 'JetBrains Mono', monospace;
    font-size: 1.1rem;
    color: #22d3ee;

    .cursor {
      animation: blink 1s step-end infinite;
    }
  }
}

@keyframes blink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0; }
}

// Stats Panel
.stats-panel {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding: 1.5rem 3rem;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  backdrop-filter: blur(10px);
  margin-bottom: 3rem;

  @media (max-width: 768px) {
    flex-direction: column;
    gap: 1rem;
    padding: 1.5rem;
  }
}

.stat-item {
  text-align: center;

  .stat-value {
    display: block;
    font-size: 2rem;
    font-weight: 700;
    color: #fff;
    font-family: 'JetBrains Mono', monospace;
  }

  .stat-label {
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.5);
    text-transform: uppercase;
    letter-spacing: 2px;
  }
}

.stat-divider {
  width: 1px;
  height: 40px;
  background: rgba(255, 255, 255, 0.2);

  @media (max-width: 768px) {
    width: 60px;
    height: 1px;
  }
}

// Scroll Indicator
.scroll-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;

  .scroll-text {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.4);
    letter-spacing: 3px;
  }

  .scroll-arrow {
    color: #6366f1;
  }
}

// Decorations
.hero-decorations {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.corner-bracket {
  position: absolute;
  width: 40px;
  height: 40px;
  border: 2px solid rgba(99, 102, 241, 0.3);

  &.top-left {
    top: 30px;
    left: 30px;
    border-right: none;
    border-bottom: none;
  }

  &.top-right {
    top: 30px;
    right: 30px;
    border-left: none;
    border-bottom: none;
  }

  &.bottom-left {
    bottom: 30px;
    left: 30px;
    border-right: none;
    border-top: none;
  }

  &.bottom-right {
    bottom: 30px;
    right: 30px;
    border-left: none;
    border-top: none;
  }
}

.scan-line {
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, rgba(99, 102, 241, 0.3), transparent);
  opacity: 0.5;
}

.grid-overlay {
  position: absolute;
  inset: 0;
  background-image:
    linear-gradient(rgba(255, 255, 255, 0.02) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255, 255, 255, 0.02) 1px, transparent 1px);
  background-size: 50px 50px;
  opacity: 0.5;
}

// Floating Data
.floating-data {
  position: absolute;
  inset: 0;
  pointer-events: none;

  .data-point {
    position: absolute;
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.7rem;
    color: rgba(99, 102, 241, 0.6);
    letter-spacing: 1px;

    @media (max-width: 768px) {
      display: none;
    }
  }
}
