.rocket-journey {
  position: fixed;
  inset: 0;
  pointer-events: none;
  z-index: 1;
}

// === LAUNCHPAD SCENE ===
.launchpad-scene {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 250px;
  height: 350px;

  .launchpad-svg {
    width: 100%;
    height: 100%;
  }

  .countdown {
    position: absolute;
    bottom: -40px;
    left: 50%;
    transform: translateX(-50%);
    font-family: 'JetBrains Mono', monospace;
    font-size: 12px;
    color: #22d3ee;
    white-space: nowrap;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-shadow: 0 0 10px rgba(34, 211, 238, 0.5);
  }
}

// === LAUNCHING ROCKET ===
.launching-rocket {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 200px;

  .rocket-launching-svg {
    width: 100%;
    height: 100%;
  }
}

// === FLYING ROCKET ===
.flying-rocket {
  position: absolute;
  width: 80px;
  height: 140px;
  transform-origin: center center;

  .rocket-svg {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 0 20px rgba(251, 191, 36, 0.4));
  }

  .engine-particles {
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 50px;
  }

  .particle {
    position: absolute;
    width: 4px;
    height: 4px;
    background: #fbbf24;
    border-radius: 50%;
    left: 50%;
    top: 0;
    box-shadow: 0 0 6px #f97316;
  }
}

// === SPACE STATION ===
.space-station {
  position: absolute;
  top: 30%;
  width: 200px;
  height: 100px;

  .station-svg {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 0 10px rgba(34, 211, 238, 0.3));
  }
}

// === SATELLITE ===
.satellite {
  position: absolute;
  top: 40%;
  width: 80px;
  height: 80px;

  .satellite-svg {
    width: 100%;
    height: 100%;
  }
}

// === COMET ===
.comet {
  position: absolute;
  inset: 0;
  overflow: hidden;

  .comet-body {
    position: absolute;
    width: 150px;
    height: 30px;
  }

  .comet-svg {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 0 10px rgba(34, 211, 238, 0.6));
  }
}

// === PORTAL/WORMHOLE ===
.portal {
  position: absolute;
  top: 20%;
  right: 15%;
  width: 200px;
  height: 200px;

  .portal-svg {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 0 30px rgba(99, 102, 241, 0.5));
  }
}

// === LANDED ROCKET ===
.landed-rocket {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  width: 350px;
  height: 250px;

  .landed-svg {
    width: 100%;
    height: 100%;
  }

  .mission-complete {
    position: absolute;
    bottom: -30px;
    left: 50%;
    transform: translateX(-50%);
    font-family: 'JetBrains Mono', monospace;
    font-size: 14px;
    color: #22d3ee;
    white-space: nowrap;
    text-transform: uppercase;
    letter-spacing: 4px;
    text-shadow: 0 0 20px rgba(34, 211, 238, 0.8);
    animation: missionPulse 2s ease-in-out infinite;
  }
}

@keyframes missionPulse {
  0%, 100% {
    opacity: 0.8;
    text-shadow: 0 0 20px rgba(34, 211, 238, 0.8);
  }
  50% {
    opacity: 1;
    text-shadow: 0 0 40px rgba(34, 211, 238, 1), 0 0 60px rgba(99, 102, 241, 0.5);
  }
}

// === RESPONSIVE ===
@media (max-width: 768px) {
  .launchpad-scene {
    width: 180px;
    height: 250px;

    .countdown {
      font-size: 10px;
    }
  }

  .launching-rocket {
    width: 70px;
    height: 140px;
  }

  .flying-rocket {
    width: 60px;
    height: 100px;
  }

  .space-station {
    width: 150px;
    height: 75px;
  }

  .satellite {
    width: 50px;
    height: 50px;
  }

  .portal {
    width: 120px;
    height: 120px;
    right: 5%;
  }

  .landed-rocket {
    width: 280px;
    height: 200px;

    .mission-complete {
      font-size: 11px;
      letter-spacing: 2px;
    }
  }
}

// === ADDITIONAL EFFECTS ===

// Starfield dust particles
.space-dust {
  position: absolute;
  width: 2px;
  height: 2px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  animation: dustFloat 10s linear infinite;
}

@keyframes dustFloat {
  0% {
    transform: translateY(0) translateX(0);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100vh) translateX(50px);
    opacity: 0;
  }
}

// Rocket trail effect
.rocket-trail {
  position: absolute;
  width: 4px;
  height: 100px;
  background: linear-gradient(to bottom, rgba(251, 191, 36, 0.6), transparent);
  filter: blur(2px);
  animation: trailFade 0.5s linear infinite;
}

@keyframes trailFade {
  0% {
    opacity: 1;
    transform: scaleY(1);
  }
  100% {
    opacity: 0;
    transform: scaleY(0.5);
  }
}
