.space-events {
  position: fixed;
  inset: 0;
  pointer-events: none;
  z-index: 50; // Above content (10) but below navbar (100)
  overflow: hidden;
}

.event {
  position: absolute;
  // Debug border - remove after testing
  // border: 2px solid red;

  .event-label {
    position: absolute;
    bottom: -30px;
    left: 50%;
    transform: translateX(-50%);
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.8);
    white-space: nowrap;
    letter-spacing: 1px;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.8), 0 0 20px rgba(0, 0, 0, 0.6);
    background: rgba(0, 0, 0, 0.5);
    padding: 4px 12px;
    border-radius: 4px;
  }
}

// === RICK AND MORTY ===
.rick-morty {
  top: 30%;
  left: 50%;
  transform: translateX(-50%);
  width: 400px;
  height: 300px;

  .portal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 200px;
    height: 200px;
    filter: drop-shadow(0 0 30px #39ff14);
  }

  .rick, .morty {
    position: absolute;
    top: 40%;
    width: 60px;
    filter: drop-shadow(0 0 10px rgba(57, 255, 20, 0.5));
  }

  .morty {
    top: 50%;
    width: 50px;
  }
}

// === IRON MAN ===
.iron-man {
  top: 40%;
  width: 150px;
  height: 100px;

  .iron-man-svg {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 0 20px rgba(255, 68, 68, 0.5));
  }

  .repulsor-trail {
    position: absolute;
    top: 50%;
    right: 100%;
    width: 200px;
    height: 30px;
    background: linear-gradient(to left, rgba(0, 191, 255, 0.6), transparent);
    filter: blur(5px);
    transform: translateY(-50%);
  }
}

// === MILLENNIUM FALCON ===
.millennium-falcon {
  top: 35%;
  left: 50%;
  transform: translateX(-50%);
  width: 300px;
  height: 180px;

  .falcon-container {
    width: 100%;
    height: 100%;
  }

  .falcon-svg {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 0 10px rgba(100, 150, 200, 0.5));
  }

  .hyperspace-lines {
    position: absolute;
    inset: -100%;
    background: repeating-linear-gradient(
      90deg,
      transparent,
      transparent 10px,
      rgba(255, 255, 255, 0.1) 10px,
      rgba(255, 255, 255, 0.3) 12px
    );
    animation: hyperspace 0.2s linear infinite;
  }
}

@keyframes hyperspace {
  0% { transform: translateX(0); }
  100% { transform: translateX(20px); }
}

// === E.T. BICYCLE ===
.et-bicycle {
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  width: 400px;
  height: 300px;

  .moon-backdrop {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 200px;
    height: 200px;
  }

  .et-silhouette {
    position: absolute;
    width: 150px;
    filter: drop-shadow(0 0 20px rgba(255, 107, 107, 0.3));
  }
}

// === BABY YODA ===
.baby-yoda {
  top: 30%;
  right: 15%;
  width: 180px;
  height: 150px;

  .grogu-svg {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 0 20px rgba(99, 102, 241, 0.4));
  }

  .event-label {
    color: #4a7c59;
  }
}

// === BIFROST ===
.bifrost {
  right: 20%;
  top: 0;
  width: 100px;
  height: 100%;

  .bifrost-beam {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    overflow: hidden;

    .beam-inner {
      position: absolute;
      inset: 0;
      background: linear-gradient(
        90deg,
        #ff0000,
        #ff7f00,
        #ffff00,
        #00ff00,
        #0000ff,
        #4b0082,
        #9400d3
      );
      opacity: 0.8;
      animation: shimmer 1s linear infinite;
    }

    .beam-particles {
      position: absolute;
      inset: 0;
    }

    .particle {
      position: absolute;
      width: 4px;
      height: 4px;
      background: #fff;
      border-radius: 50%;
    }
  }

  .thor-silhouette {
    position: absolute;
    bottom: 10%;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.5));
  }
}

@keyframes shimmer {
  0% { filter: hue-rotate(0deg); }
  100% { filter: hue-rotate(360deg); }
}

// === TARDIS ===
.tardis {
  left: 15%;
  top: 25%;
  width: 100px;
  height: 180px;

  .tardis-box {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 0 30px rgba(0, 59, 111, 0.8));
  }

  .tardis-glow {
    position: absolute;
    inset: -20px;
    background: radial-gradient(ellipse, rgba(0, 100, 200, 0.3), transparent 70%);
    animation: tardisGlow 2s ease-in-out infinite;
  }
}

@keyframes tardisGlow {
  0%, 100% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 0.6; transform: scale(1.1); }
}

// === AMONG US ===
.among-us {
  top: 40%;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 120px;

  .crewmate {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 0 15px rgba(255, 0, 0, 0.5));
  }

  .event-label {
    color: #ff6b6b;
    font-style: italic;
  }
}

// === THANOS SNAP ===
.thanos-snap {
  top: 30%;
  left: 50%;
  transform: translateX(-50%);
  width: 300px;
  height: 300px;

  .gauntlet {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120px;
    filter: drop-shadow(0 0 30px rgba(255, 215, 0, 0.6));
  }

  .dust-particles {
    position: absolute;
    inset: 0;
  }

  .dust {
    position: absolute;
    width: 8px;
    height: 8px;
    background: linear-gradient(135deg, #f39c12, #e74c3c);
    border-radius: 2px;
  }

  .event-label {
    color: #9b59b6;
    font-weight: bold;
  }
}

// === RESPONSIVE ===
@media (max-width: 768px) {
  .rick-morty {
    width: 280px;
    height: 200px;

    .portal {
      width: 140px;
      height: 140px;
    }

    .rick { width: 45px; }
    .morty { width: 35px; }
  }

  .iron-man {
    width: 100px;
    height: 70px;
  }

  .millennium-falcon {
    width: 200px;
    height: 120px;
  }

  .et-bicycle {
    width: 280px;
    height: 200px;

    .moon-backdrop {
      width: 140px;
      height: 140px;
    }

    .et-silhouette {
      width: 100px;
    }
  }

  .baby-yoda {
    width: 120px;
    height: 100px;
    right: 5%;
  }

  .bifrost {
    right: 10%;
    width: 60px;
  }

  .tardis {
    width: 70px;
    height: 120px;
    left: 5%;
  }

  .among-us {
    width: 70px;
    height: 85px;
  }

  .thanos-snap {
    width: 200px;
    height: 200px;

    .gauntlet {
      width: 80px;
    }
  }

  .event-label {
    font-size: 0.6rem !important;
    display: none; // Hide labels on mobile for cleaner look
  }
}
