.projects-section-dark {
  min-height: 100vh;
  padding: 6rem 2rem;
  position: relative;
}

.projects-container {
  max-width: 1200px;
  margin: 0 auto;
}

// Section Header
.section-header {
  text-align: center;
  margin-bottom: 4rem;

  .section-number {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.85rem;
    color: #6366f1;
    letter-spacing: 4px;
    display: block;
    margin-bottom: 0.5rem;
  }

  .section-title {
    font-size: 2.5rem;
    font-weight: 700;
    color: #fff;
    margin-bottom: 0.5rem;
  }

  .section-subtitle {
    color: rgba(255, 255, 255, 0.5);
    font-size: 1rem;
  }
}

// Missions Grid
.missions-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  margin-bottom: 4rem;

  @media (max-width: 968px) {
    grid-template-columns: 1fr;
  }
}

// Mission Card
.mission-card {
  position: relative;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  overflow: hidden;
  transition: all 0.3s ease;

  &:hover {
    border-color: rgba(99, 102, 241, 0.4);
    box-shadow: 0 20px 60px rgba(99, 102, 241, 0.1);

    .mission-icon {
      .icon-ring {
        opacity: 1;
      }
    }
  }

  .card-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 1.5rem;
    background: rgba(99, 102, 241, 0.05);
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  }

  .mission-icon {
    position: relative;
    width: 50px;
    height: 50px;
    background: rgba(99, 102, 241, 0.1);
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #6366f1;
    font-size: 1.25rem;

    .icon-ring {
      position: absolute;
      inset: -4px;
      border: 2px solid transparent;
      border-top-color: #6366f1;
      border-radius: 14px;
      opacity: 0;
      transition: opacity 0.3s;
    }
  }

  .mission-meta {
    text-align: right;

    .mission-id {
      display: block;
      font-family: 'JetBrains Mono', monospace;
      font-size: 0.7rem;
      color: rgba(255, 255, 255, 0.4);
      letter-spacing: 1px;
      margin-bottom: 0.5rem;
    }

    .mission-status {
      display: inline-flex;
      align-items: center;
      gap: 0.4rem;
      font-family: 'JetBrains Mono', monospace;
      font-size: 0.65rem;
      padding: 0.25rem 0.6rem;
      border-radius: 4px;
      letter-spacing: 1px;

      .status-dot {
        width: 6px;
        height: 6px;
        border-radius: 50%;
        animation: pulse 2s infinite;
      }

      &.operational {
        background: rgba(34, 197, 94, 0.15);
        color: #22c55e;

        .status-dot {
          background: #22c55e;
        }
      }
    }
  }

  .card-body {
    padding: 1.5rem;
  }

  .mission-codename {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.7rem;
    color: #22d3ee;
    letter-spacing: 2px;
    display: block;
    margin-bottom: 0.5rem;
  }

  .mission-title {
    font-size: 1.25rem;
    font-weight: 600;
    color: #fff;
    margin-bottom: 0.75rem;
  }

  .mission-description {
    color: rgba(255, 255, 255, 0.6);
    font-size: 0.9rem;
    line-height: 1.6;
    margin-bottom: 1.5rem;
  }

  // Metrics
  .mission-metrics {
    display: flex;
    gap: 1.5rem;
    margin-bottom: 1.5rem;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.2);
    border-radius: 8px;

    .metric {
      text-align: center;
      flex: 1;

      .metric-value {
        display: block;
        font-size: 1.25rem;
        font-weight: 700;
        color: #fff;
        font-family: 'JetBrains Mono', monospace;
      }

      .metric-label {
        font-size: 0.65rem;
        color: rgba(255, 255, 255, 0.5);
        text-transform: uppercase;
        letter-spacing: 1px;
      }
    }
  }

  // Technologies
  .mission-tech {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 1.5rem;

    .tech-tag {
      font-family: 'JetBrains Mono', monospace;
      font-size: 0.7rem;
      padding: 0.25rem 0.6rem;
      background: rgba(99, 102, 241, 0.1);
      border: 1px solid rgba(99, 102, 241, 0.2);
      border-radius: 4px;
      color: #a5b4fc;
    }
  }

  // Highlights
  .mission-highlights {
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      display: flex;
      align-items: flex-start;
      gap: 0.5rem;
      color: rgba(255, 255, 255, 0.7);
      font-size: 0.85rem;
      margin-bottom: 0.5rem;
      line-height: 1.4;

      .highlight-marker {
        color: #22d3ee;
        flex-shrink: 0;
      }
    }
  }

  // Scan effect
  .scan-effect {
    position: absolute;
    left: 0;
    right: 0;
    height: 100px;
    background: linear-gradient(
      to bottom,
      transparent,
      rgba(99, 102, 241, 0.05),
      transparent
    );
    pointer-events: none;
  }
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

// Mission Summary
.mission-summary {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;

  @media (max-width: 600px) {
    flex-direction: column;
    gap: 1.5rem;
  }

  .summary-item {
    text-align: center;

    .summary-value {
      display: block;
      font-size: 2rem;
      font-weight: 700;
      color: #fff;
      font-family: 'JetBrains Mono', monospace;
    }

    .summary-label {
      font-size: 0.75rem;
      color: rgba(255, 255, 255, 0.5);
      text-transform: uppercase;
      letter-spacing: 2px;
    }
  }

  .summary-divider {
    width: 1px;
    height: 50px;
    background: rgba(255, 255, 255, 0.1);

    @media (max-width: 600px) {
      width: 80px;
      height: 1px;
    }
  }
}
