.journey-section-dark {
  min-height: 100vh;
  padding: 6rem 2rem;
  position: relative;
}

.journey-container {
  max-width: 1000px;
  margin: 0 auto;
}

// Section Header
.section-header {
  text-align: center;
  margin-bottom: 4rem;

  .section-number {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.85rem;
    color: #6366f1;
    letter-spacing: 4px;
    display: block;
    margin-bottom: 0.5rem;
  }

  .section-title {
    font-size: 2.5rem;
    font-weight: 700;
    color: #fff;
    margin-bottom: 0.5rem;
  }

  .section-subtitle {
    color: rgba(255, 255, 255, 0.5);
    font-size: 1rem;
  }
}

// Mission Timeline
.mission-timeline {
  position: relative;
  padding: 2rem 0;
}

.timeline-line {
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background: linear-gradient(to bottom, #6366f1, #22d3ee, #22c55e);
  transform-origin: top;

  @media (max-width: 768px) {
    left: 20px;
  }
}

// Mission Entry
.mission-entry {
  display: flex;
  align-items: flex-start;
  margin-bottom: 3rem;
  position: relative;

  &:nth-child(odd) {
    flex-direction: row;
    padding-right: calc(50% + 40px);

    .timeline-node {
      right: calc(50% - 20px);
    }

    @media (max-width: 768px) {
      padding-right: 0;
      padding-left: 60px;

      .timeline-node {
        left: 0;
        right: auto;
      }
    }
  }

  &:nth-child(even) {
    flex-direction: row-reverse;
    padding-left: calc(50% + 40px);

    .timeline-node {
      left: calc(50% - 20px);
    }

    @media (max-width: 768px) {
      flex-direction: row;
      padding-left: 60px;
      padding-right: 0;

      .timeline-node {
        left: 0;
      }
    }
  }

  &.active {
    .mission-card {
      border-color: rgba(34, 197, 94, 0.3);
      background: rgba(34, 197, 94, 0.05);
    }
  }
}

// Timeline Node
.timeline-node {
  position: absolute;
  width: 40px;
  height: 40px;
  background: #0a0a1a;
  border: 2px solid #6366f1;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #6366f1;
  font-size: 1rem;
  z-index: 10;

  .node-pulse {
    position: absolute;
    inset: -5px;
    border: 2px solid #22c55e;
    border-radius: 50%;
  }

  .active & {
    border-color: #22c55e;
    color: #22c55e;
  }
}

// Mission Card
.mission-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 1.5rem;
  flex: 1;
  transition: all 0.3s ease;

  &:hover {
    border-color: rgba(99, 102, 241, 0.3);
    transform: translateY(-2px);
  }

  .card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
  }

  .mission-year {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.8rem;
    color: #22d3ee;
    letter-spacing: 1px;
  }

  .mission-status {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.65rem;
    padding: 0.25rem 0.5rem;
    border-radius: 4px;
    letter-spacing: 1px;

    &.completed {
      background: rgba(99, 102, 241, 0.2);
      color: #6366f1;
    }

    &.active {
      background: rgba(34, 197, 94, 0.2);
      color: #22c55e;
      animation: statusBlink 2s infinite;
    }
  }

  .mission-title {
    font-size: 1.25rem;
    font-weight: 600;
    color: #fff;
    margin-bottom: 0.5rem;
  }

  .mission-meta {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 1rem;
    flex-wrap: wrap;

    .mission-role {
      color: rgba(255, 255, 255, 0.8);
      font-size: 0.9rem;
    }

    .mission-divider {
      color: rgba(255, 255, 255, 0.3);
    }

    .mission-location {
      color: rgba(255, 255, 255, 0.5);
      font-size: 0.9rem;
    }
  }

  .mission-description {
    color: rgba(255, 255, 255, 0.6);
    font-size: 0.9rem;
    line-height: 1.6;
  }

  // Progress bar for active mission
  .mission-progress {
    margin-top: 1.5rem;
    padding-top: 1rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);

    .progress-label {
      display: flex;
      justify-content: space-between;
      margin-bottom: 0.5rem;
      font-size: 0.75rem;
      color: rgba(255, 255, 255, 0.5);

      .progress-value {
        color: #22c55e;
        font-family: 'JetBrains Mono', monospace;
      }
    }

    .progress-bar {
      height: 4px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 2px;
      overflow: hidden;
    }

    .progress-fill {
      height: 100%;
      background: linear-gradient(90deg, #22c55e, #22d3ee);
      border-radius: 2px;
    }
  }
}

@keyframes statusBlink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}

// Mission Stats
.mission-stats {
  display: flex;
  justify-content: center;
  gap: 4rem;
  margin-top: 4rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 12px;

  @media (max-width: 600px) {
    flex-direction: column;
    gap: 1.5rem;
    align-items: center;
  }

  .stat-item {
    text-align: center;

    .stat-value {
      display: block;
      font-size: 2.5rem;
      font-weight: 700;
      color: #fff;
      font-family: 'JetBrains Mono', monospace;
    }

    .stat-label {
      font-size: 0.8rem;
      color: rgba(255, 255, 255, 0.5);
      text-transform: uppercase;
      letter-spacing: 2px;
    }
  }
}

// Trajectory Visualization
.trajectory-viz {
  margin-top: 3rem;
  text-align: center;

  .trajectory-svg {
    width: 100%;
    max-width: 800px;
    height: 100px;
  }

  .trajectory-label {
    display: block;
    margin-top: 1rem;
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.4);
    letter-spacing: 2px;
  }
}
